import { Container, Typography, Button, Stack } from "@mui/material";
import PeopleIcon from "@mui/icons-material/People";
import InfoIcon from "@mui/icons-material/Info";
import { formatDateTime, DateFormat } from "@actnowcoalition/time-utils";
import {
  MetricOverview,
  MetricSparklines,
} from "@actnowcoalition/ui-components";
import { metricCatalog } from "../../utils/metrics";
import { useParams } from "react-router-dom";
import { getRegionFromSlugStrict } from "../../utils/routing";

export const Location: React.FC = () => {
  const { stateId } = useParams();
  const region = getRegionFromSlugStrict(stateId ?? "washington-wa");
  return (
    <Container>
      <Stack
        direction="row"
        justifyContent="space-between"
        alignItems="flex-start"
        marginY={6}
      >
        <Stack direction="column">
          <Typography variant="h1">{region.shortName}</Typography>
          <Typography variant="paragraphSmall">
            Updated on {formatDateTime(new Date(), DateFormat.MMMM_D)}
          </Typography>
        </Stack>
        <Stack direction="row" spacing={1}>
          <Button variant="outlined" endIcon={<PeopleIcon />}>
            Share
          </Button>
          <Button variant="contained" endIcon={<InfoIcon />}>
            Get Alerts
          </Button>
        </Stack>
      </Stack>
      <Stack direction="row" spacing={6}>
        {metricCatalog.metrics.map((metric) => {
          return (
            <MetricOverview
              key={`metric-item-${metric.id}`}
              region={region}
              metric={metric}
              supportingText={metric.extendedName}
              metricChart={
                <MetricSparklines
                  region={region}
                  metricLineChart={metric}
                  metricBarChart={metric}
                  numDays={30}
                />
              }
            />
          );
        })}
      </Stack>
    </Container>
  );
};
