import React from 'react';
import logo from './logo.svg';
import './App.css';
import createEmotionCache from './styles/createEmotionCache';
import { CacheProvider } from '@emotion/react';
import theme from './styles/theme';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { MetricCatalogProvider } from '@actnowcoalition/ui-components';
import { metricCatalog } from './utils/metrics';
import Homepage from './screens/Homepage';
import { Location } from './screens/Location';
import { Route, Routes } from 'react-router-dom';

// Client-side cache, shared for the whole session of the user in the browser.
const clientSideEmotionCache = createEmotionCache();

function App() {
  return (
    <CacheProvider value={clientSideEmotionCache}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <MetricCatalogProvider metricCatalog={metricCatalog}>
          <Routes>
            <Route path="/" element={<Homepage />} />
            <Route path="/us/:stateId" element={<Location />} />
          </Routes>
        </MetricCatalogProvider>
      </ThemeProvider>
    </CacheProvider>
  );
}

export default App;
