import { Region, RegionDB, states } from "@actnowcoalition/regions";

// Create a new set of states with a custom relativeUrl
const updatedStates = states.all.map((state) => {
  return Region.fromJSON({
    ...state.toJSON(),
    relativeUrl: `/us/${state.slug}`,
  });
});

// Customize the set of regions your Act Now site will support. Make sure to
// update `relativeUrl` to match the directory structure of your pages.
export const regions = new RegionDB(updatedStates);
