import { useState } from "react";
import {
  Container,
  ToggleButton,
  ToggleButtonGroup,
  Box,
  Typography,
  Stack,
  Paper,
} from "@mui/material";
import {
  RegionSearch,
  MetricLegendThreshold,
  MetricUSNationalMap,
  MetricCompareTable,
} from "@actnowcoalition/ui-components";
import { regions } from "../../utils/regions";
import { MetricId } from "../../utils/metrics";

const Homepage: React.FC = () => {
  const [mapView, setMapView] = useState<string>("states");

  const handleChange = (e: React.MouseEvent, newValue: string) => {
    if (newValue !== null) {
      setMapView(newValue);
    }
  };

  return (
    <Container>
      <Stack marginTop={{ xs: 8 }} spacing={6} alignItems="center">
        <Stack>
          <Typography variant="h1" align="center">
            Monkeypox is spreading quickly
          </Typography>
          <Typography variant="paragraphLarge" align="center">
            Its now in 49 states. See where the contagious virus is spreading.
          </Typography>
        </Stack>
        <Box sx={{ width: 550 }}>
          <RegionSearch options={regions.all} inputLabel="States" />
        </Box>
        <ToggleButtonGroup
          exclusive
          onChange={handleChange}
          value={mapView}
          size="small"
        >
          <ToggleButton value="counties" disabled>
            Counties
          </ToggleButton>
          <ToggleButton value="states">States</ToggleButton>
        </ToggleButtonGroup>
        <Box sx={{ position: "relative", width: 800, height: 420 }}>
          <MetricUSNationalMap
            metric={MetricId.MONKEYPOX_CASES}
            width={800}
            renderTooltip={(regionId) =>
              regions.findByRegionIdStrict(regionId).fullName
            }
            showCounties={mapView === "counties"}
          />
        </Box>
        <MetricLegendThreshold
          orientation="horizontal"
          metric={MetricId.MONKEYPOX_CASES}
          startLabel={
            <Typography variant="paragraphSmall" color="secondary.dark">
              <b>Low</b> risk
            </Typography>
          }
          endLabel={
            <Typography variant="paragraphSmall" color="secondary.dark">
              <b>High</b> risk
            </Typography>
          }
        />
        <Typography variant="h2">Compare Locations</Typography>
        <Paper style={{ height: 600, overflow: "auto" }}>
          <MetricCompareTable
            regions={regions.all}
            metrics={[
              MetricId.METRIC1,
              MetricId.METRIC1,
              MetricId.MONKEYPOX_CASES,
            ]}
          />
        </Paper>
      </Stack>
      {/* just for spacing */}
      <Box sx={{ height: 600 }} />
    </Container>
  );
};

export default Homepage;
